var formatDistanceLocale = {
  lessThanXSeconds: {
    one: '   ',
    two: '  ',
    threeToTen: '  {{count}} ',
    other: '  {{count}} '
  },

  xSeconds: {
    one: ' ',
    two: '',
    threeToTen: '{{count}} ',
    other: '{{count}} '
  },

  halfAMinute: ' ',

  lessThanXMinutes: {
    one: '  ',
    two: '  ',
    threeToTen: '  {{count}} ',
    other: '  {{count}} '
  },

  xMinutes: {
    one: ' ',
    two: '',
    threeToTen: '{{count}} ',
    other: '{{count}} '
  },

  aboutXHours: {
    one: '  ',
    two: ' ',
    threeToTen: '{{count}}  ',
    other: '{{count}}  '
  },

  xHours: {
    one: ' ',
    two: '',
    threeToTen: '{{count}} ',
    other: '{{count}} '
  },

  xDays: {
    one: ' ',
    two: '',
    threeToTen: '{{count}} ',
    other: '{{count}} '
  },

  aboutXMonths: {
    one: '  ',
    two: ' ',
    threeToTen: '{{count}}  ',
    other: '{{count}}  '
  },

  xMonths: {
    one: ' ',
    two: '',
    threeToTen: '{{count}} ',
    other: '{{count}} '
  },

  aboutXYears: {
    one: '  ',
    two: ' ',
    threeToTen: '{{count}}  ',
    other: '{{count}}  '
  },

  xYears: {
    one: ' ',
    two: '',
    threeToTen: '{{count}} ',
    other: '{{count}} '
  },

  overXYears: {
    one: '  ',
    two: '  ',
    threeToTen: '  {{count}} ',
    other: '  {{count}} '
  },

  almostXYears: {
    one: '  ',
    two: ' ',
    threeToTen: '{{count}}  ',
    other: '{{count}}  '
  }
}

export default function formatDistance(token, count, options) {
  options = options || {}

  var result
  if (typeof formatDistanceLocale[token] === 'string') {
    result = formatDistanceLocale[token]
  } else if (count === 1) {
    result = formatDistanceLocale[token].one
  } else if (count === 2) {
    result = formatDistanceLocale[token].two
  } else if (count <= 10) {
    result = formatDistanceLocale[token].threeToTen.replace('{{count}}', count)
  } else {
    result = formatDistanceLocale[token].other.replace('{{count}}', count)
  }

  if (options.addSuffix) {
    if (options.comparison > 0) {
      return '  ' + result
    } else {
      return ' ' + result
    }
  }

  return result
}
