var formatRelativeLocale = {
  lastWeek: "'pasinta' eeee 'je' p",
  yesterday: "'hiera je' p",
  today: "'hodia je' p",
  tomorrow: "'morga je' p",
  nextWeek: "eeee 'je' p",
  other: 'P'
}

export default function formatRelative (token, date, baseDate, options) {
  return formatRelativeLocale[token]
}
