var formatDistanceLocale = {
  lessThanXSeconds: {
    one: ' ',
    two: '  ',
    other: ' {{count}} '
  },

  xSeconds: {
    one: '',
    two: ' ',
    other: '{{count}} '
  },

  halfAMinute: ' ',

  lessThanXMinutes: {
    one: ' ',
    two: '  ',
    other: ' {{count}} '
  },

  xMinutes: {
    one: '',
    two: ' ',
    other: '{{count}} '
  },

  aboutXHours: {
    one: ' ',
    two: ' ',
    other: ' {{count}} '
  },

  xHours: {
    one: '',
    two: '',
    other: '{{count}} '
  },

  xDays: {
    one: '',
    two: '',
    other: '{{count}} '
  },

  aboutXMonths: {
    one: ' ',
    two: ' ',
    other: ' {{count}} '
  },

  xMonths: {
    one: '',
    two: '',
    other: '{{count}} '
  },

  aboutXYears: {
    one: ' ',
    two: ' ',
    other: ' {{count}} '
  },

  xYears: {
    one: '',
    two: '',
    other: '{{count}} '
  },

  overXYears: {
    one: ' ',
    two: ' ',
    other: ' {{count}} '
  },

  almostXYears: {
    one: ' ',
    two: ' ',
    other: ' {{count}} '
  }
}

export default function formatDistance (token, count, options) {
  options = options || {}

  // Return word instead of `in one day` or `one day ago`
  if (token === 'xDays' && options.addSuffix && count <= 2) {
    var past = {
      1: '',
      2: ''
    }
    var future = {
      1: '',
      2: ''
    }
    return options.comparison > 0 ? future[count] : past[count]
  }

  var result
  if (typeof formatDistanceLocale[token] === 'string') {
    result = formatDistanceLocale[token]
  } else if (count === 1) {
    result = formatDistanceLocale[token].one
  } else if (count === 2) {
    result = formatDistanceLocale[token].two
  } else {
    result = formatDistanceLocale[token].other.replace('{{count}}', count)
  }

  if (options.addSuffix) {
    if (options.comparison > 0) {
      return ' ' + result
    } else {
      return ' ' + result
    }
  }

  return result
}
