import localize from '../localize/index.js'

var formatDistanceLocale = {
  lessThanXSeconds: {
    one: '  ',
    other: ' {{count}} '
  },

  xSeconds: {
    one: ' ',
    other: '{{count}} '
  },

  halfAMinute: ' ',

  lessThanXMinutes: {
    one: '  ',
    other: ' {{count}} '
  },

  xMinutes: {
    one: ' ',
    other: '{{count}} '
  },

  aboutXHours: {
    one: '  ',
    other: ' {{count}} '
  },

  xHours: {
    one: ' ',
    other: '{{count}} '
  },

  xDays: {
    one: ' ',
    other: '{{count}} '
  },

  aboutXMonths: {
    one: '  ',
    other: ' {{count}} '
  },

  xMonths: {
    one: ' ',
    other: '{{count}} '
  },

  aboutXYears: {
    one: '  ',
    other: ' {{count}} '
  },

  xYears: {
    one: ' ',
    other: '{{count}} '
  },

  overXYears: {
    one: '  ',
    other: '{{count}}  '
  },

  almostXYears: {
    one: '  ',
    other: ' {{count}} '
  }
}

export default function formatDistance (token, count, options) {
  options = options || {}

  var result
  if (typeof formatDistanceLocale[token] === 'string') {
    result = formatDistanceLocale[token]
  } else if (count === 1) {
    result = formatDistanceLocale[token].one
  } else {
    result = formatDistanceLocale[token].other.replace('{{count}}', localize.numberToLocale(count))
  }

  if (options.addSuffix) {
    if (options.comparison > 0) {
      return result + '  '
    } else {
      return result + ' '
    }
  }

  return result
}
